body {
  font-family: sans-serif;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-grow: 1;
}

.app {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.gamepad {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.gamepadTitle > h2 > .gamepadTitle__subtitle {
  font-size: 50%;
  color: rgb(160, 160, 160);
  vertical-align: middle;
}

.buttons {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
  width: 500px;
  flex-wrap: wrap;
  flex-grow: 1;
}

.button {
  border: 1px solid black;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5px;
  color: #5a5a5a;
}

.axes {
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 10px;
}

.axis {
  position: relative;
  height: 200px;
  width: 200px;
}

.axes__container {
  display: flex;
  flex-direction: row;
}

.timestamp {
  position: absolute;
  bottom: 10px;
  font-size: 8pt;
  color: #8a8a8a;
}

.vibration {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.message {
  text-align: center;
}

button {
  background-color: rgba(255, 255, 255, 0);
  border-radius: 5px;
  border: 1px solid #000;
  padding: 10px;
  transition: 0.2s all linear;
  font-size: 12px;
}

button:hover {
  background-color: rgb(209, 55, 55);
  border: none;
  color: #fff;
  animation: shake 0.62s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
}

@keyframes shake {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-2px, 0, 0);
  }

  40%,
  60% {
    transform: translate3d(2px, 0, 0);
  }
}
